
var jsonp = require("jsonp");
import Panel from 'vux/src/components/panel/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default{
data(){
return {
show:false,
title:"",
list:[],
start:0,
count:10,
}
},
components:{
Panel,
Swipeout,
SwipeoutItem,
SwipeoutButton,
},
methods:{
jump(id){

this.$router.push({path:"/detail/"+id})
},
getData(){
var that = this;

let keyWord,target;
if( this.$route.params.id == "hotList" ){
keyWord = "in_theaters";
this.title = "影院热映";
}
if( this.$route.params.id == "comingList" ){
keyWord = "coming_soon";
this.title = "即将上映";
}
jsonp('https://api.douban.com/v2/movie/'+ keyWord +'?start='+this.start+'&count='+this.count,null,function(res,data){
console.log( data );
that.list = data.subjects.map( (item)=>{
return {


id:item.id,
src: item.images.large,
title: item.title,
desc: "主演："+item.casts.map((a)=>{ return a.name }).join("、")
}
} );
console.log( that.list );
that.$vux.loading.hide();
that.show = true;
})
},
re(){
this.start = 0;
this.count = 10;
this.$vux.toast.show({
text: '刷新完成',
time: 1000,
})
localStorage.clear();
this.getData();
this.$refs.my_scroller.finishPullToRefresh();
},
loadMore:function(done){

this.start += this.count;
this.count = 3;

this.$refs.my_scroller.finishInfinite(function(){
console.log(1)
})

}
},
created(){
this.$vux.loading.show({
text: 'Loading'
})
this.getData();
}
}



