
var jsonp = require("jsonp");
import Panel from 'vux/src/components/panel/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
data(){
return {
showContent:true,
movieList : [],
}
},
components:{
Panel,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Cell,
Group,
},
methods:{
del(index,id){
this.movieList.splice( index,1 );
let data = JSON.parse(localStorage.getItem( "movie" ));
data = data.filter( a =>{

return !a[id];
} )
this.$vux.toast.show({
text: '取消成功',
time: 800,
})
localStorage.setItem( "movie",JSON.stringify( data ) );

},
toggleShow(){
this.showContent = !this.showContent;
}

},
created(){
let data = JSON.parse(localStorage.getItem( "movie" )) || [] ;

this.movieList = [];
data.forEach( a=>{
for(var id in a){
console.log( a[id] )
this.movieList.push( a[id] );
}
} )

}
}



