export default{
	props:["isColl"],
	methods:{
		click(){
			this.$emit("clickBtn");
		}
	},
	computed:{
		text:function(){
			return this.isColl ? "已收藏" : "收藏";
		},
		classList:function(){
			return this.isColl ? "collected" : "normal";
		}
	}
}



